.moonglobe {
    position: relative;
    left: 310px;
      
      
    }
    .dropbtn {
      background-color: rgb(44, 92, 197);
      color: white;
      padding:9px;
      font-size: 14px;
      border: none;
      
      
    }
    
    .dropdown {
      position: relative;
      left: 1140px;
      top: -500px;
      display: inline-block;
      
    }
    
    .dropdown-content {
      display: none;
      position: relative;
      background-color: #f1f1f1;
      min-width: 120px;
      box-shadow: 0px 0px 0px 0px rgba(0,0,0,0.2);
      z-index: 1;
    }
    
    .dropdown-content a {
      color: black;
      padding: 12px 16px;
      text-decoration: none;
      display: block;
    }
    
    .dropdown-content a:hover {background-color: #ddd;}
    
    .dropdown:hover 
    .dropdown-content {display: block;}
    
    .dropdown:hover 
    .dropbtn {background-color:rgb(44, 92, 197);}


    
    